import React, { Component } from "react";
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import * as UI from './style';
import FullBanner from '../../components/FullBanner';
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import 'react-toastify/dist/ReactToastify.css';
import Slider from "react-slick";

export default class Index extends Component {
    settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,
        arrows:false,
        autoplay: true,
        speed: 4000,
        autoplaySpeed: 2000,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                  slidesToShow: 5,
                  slidesToScroll: 5,
                  infinite: true,
                  dots: true
                }
            },
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 991,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      };
    render(){
        return (
            <div>
                <UI.Wrapper>
                    <Header/>
                    <FullBanner>
                        <div className="banner-content d-flex justify-content-center align-items-center h-100 pt-5">
                            <div className="pt-4 container">
                                <div className="col-lg-11 mx-auto">
                                    <div className="row">
                                        <h2 className="text-center program-desc col-xl-11 col-lg-12 mx-auto mb-xl-5 mb-4">Take a deep breathe, then allow us help you from here</h2>
                                        <p className="small-heading mb-xl-5 mb-4 text-center col-lg-11 mx-auto">Social media solved the problem of connection, we are solving the problem of loneliness. Get paired with our Qiiper Empaths for emotional support today.</p>
                                        <div className=" d-flex flex-wrap mx-auto justify-content-center justify-content-md-center download-links">
                                            <a href="" className="mr-3 mr-md-5 mb-3 mb-md-0"><img src={require('../../assets/images/playstore.svg')} alt="" /></a>
                                            <a href="" className=""><img src={require('../../assets/images/appstore.svg')} alt="" /></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </FullBanner>

                    <section  className="app-features py-5">
                        <div style={{
                            backgroundImage:`url(${require(`../../assets/images/cloud.svg`)})`,
                            position:"absolute", 
                            top:"0",
                            right:"0",
                            left:"0",
                            bottom:"0"
                        }} className="bg">
                        </div>

                        <div className="container pt-4">
                            <div className="row">
                                <div className="col-lg-6 features-content mb-0 mb-lg-0">
                                    <div className="left mb-md-4 mb-lg-0 ">
                                        <div className="phones ">
                                            <img src={require('../../assets/images/phone.png')} alt="" className="phone-1 col-11 col-md-9 col-lg-11 ml-lg-n5 ml-md-5 px-0"/> 
                                            {/* <img src={require('../../assets/images/phone-2.svg')} alt="" className="phone-2"/>  */}
                                        </div>
                                    </div>
                                </div>

                                <div className="col-lg-6">
                                    <div className="right py-5">
                                        <h4 className="mb-4">Download <span><img src={require('../../assets/images/logo.svg')} alt="" className=""/> </span> Mobile</h4>
                                        <p className="app-desc section-desc">With the Qiiper Mobile App, you get to interact with different Qiiper Empaths who can assist you in your journey of emotional growth and stability.</p>
                                        <h6 className="mb-4">Features</h6>
                                        <ul className="m-0 p-0 mb-5">
                                            <li className="d-flex align-items-center mb-4">
                                                <span className=" mr-4 "><img src={require('../../assets/images/Chat.svg')} alt="" className=""/></span> <span>Chat with Qiiper Empaths</span>
                                            </li>
                                            <li className="d-flex align-items-center mb-4">
                                                <span className=" mr-4"><img src={require('../../assets/images/Call.svg')} alt="" className=""/></span> <span>Audio Call with Qiiper Empaths</span>
                                            </li>

                                            <li className="d-flex align-items-center">
                                                <span className=" mr-4"><img src={require('../../assets/images/Call.svg')} alt="" className=""/></span> <span>Video Call with Qiiper Empaths</span>
                                            </li>
                                        </ul>

                                        <div className=" d-flex download-app mx-auto justify-content-center">
                                            <a href="" className="mr-4"><img src={require('../../assets/images/playstore.svg')} alt="" /></a>
                                            <a href="" className=""><img src={require('../../assets/images/appstore.svg')} alt="" /></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </section>

                    <section  className="testimonials py-5">
                        <div className="small-cloud">
                            <img src={require('../../assets/images/small-cloud-testimonial.svg')} alt="" />
                        </div>
                        <div className="container">
                            <div className="row flex-column-reverse flex-md-row py-5 mb-5 align-items-center">
                                <div className="col-md-6">
                                    <div>
                                        <h4 className="mb-4 text-white">Get Paired with a <span><img src={require('../../assets/images/logo.svg')} alt="" className=""/> </span> Empath</h4>
                                        <p className="app-desc section-desc">With the Qiiper Mobile App, you get to interact with different Qiiper Empaths who can assist you in your journey of emotional growth and stability.</p>
                                        <div className="get-started">
                                            <a href=""><p className="mb-0 py-2 px-5 button">Get Started</p></a>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-6 top-right mb-4 mb-md-0 px-0 px-md-3">
                                    <div className="content">
                                        <img src={require('../../assets/images/illustration.svg')} alt="" className="col-lg-9 col-8 mr-md-5 ml-md-auto mx-auto d-block"/>
                                    
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="testimonies container pr-0 pb-5">
                            {/* <div className=""> */}
                           

                            <Slider {...this.settings}>
                                <div className="testimonial-item">
                                    <div className="testimonial-content pr-5">
                                        <div className="testimony px-3 mb-4 py-4 d-flex align-items-center">
                                            <p className="mb-0"> The Qiiper Empath I spoke with was so gentle and precise in our discussions.</p>
                                           </div>
                                        <div className="d-flex meta align-items-center">
                                            <div className="mr-3">
                                                <img src={require('../../assets/images/person-1.png')} alt="" className="person"/>
                                            </div>
                                            <div className="bio">
                                                <h6 className="mb-1">Kiki</h6>
                                                <p className="mb-0">New Jersey, USA</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="testimonial-item">
                                    <div className="testimonial-content pr-5">
                                        <div className="testimony px-3 mb-4 py-4 d-flex align-items-center">
                                            <p className="mb-0">Thank you Qiiper for setting up this platform. Just Amazing.</p>
                                           </div>
                                        <div className="d-flex meta align-items-center">
                                            <div className="mr-3">
                                                <img src={require('../../assets/images/person-2.png')} alt="" className="person"/>
                                            </div>
                                            <div className="bio">
                                                <h6 className="mb-1"> Janet Briggs</h6>
                                                <p className="mb-0">VC, USA</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="testimonial-item">
                                    <div className="testimonial-content pr-5">
                                        <div className="testimony px-3 mb-4 py-4 d-flex align-items-center">
                                            <p className="mb-0"> I spoke with a professional today and we spoke like we had known each other for a while. I enjoyed it.</p>
                                           </div>
                                        <div className="d-flex meta align-items-center">
                                            <div className="mr-3">
                                                <img src={require('../../assets/images/person-3.png')} alt="" className="person"/>
                                            </div>
                                            <div className="bio">
                                                <h6 className="mb-1">Edward Carlton</h6>
                                                <p className="mb-0">Toronto, Canada</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="testimonial-item">
                                    <div className="testimonial-content pr-5">
                                        <div className="testimony px-3 mb-4 py-4 d-flex align-items-center">
                                            <p className="mb-0"> Definitely coming back to this app. Qiiper is awesome.</p>
                                           </div>
                                        <div className="d-flex meta align-items-center">
                                            <div className="mr-3">
                                                <img src={require('../../assets/images/person-4.png')} alt="" className="person"/>
                                            </div>
                                            <div className="bio">
                                                <h6 className="mb-1">June Chu</h6>
                                                <p className="mb-0">Sheffield, UK</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="testimonial-item">
                                    <div className="testimonial-content pr-4">
                                        <div className="testimony px-3 mb-4 py-4 d-flex align-items-center">
                                            <p className="mb-0"> Today has been one of the best days of my life because I am now so stronger emotionally.</p>
                                           </div>
                                        <div className="d-flex meta align-items-center">
                                            <div className="mr-3">
                                                <img src={require('../../assets/images/person-5.png')} alt="" className="person"/>
                                            </div>
                                            <div className="bio">
                                                <h6 className="mb-1">Maui Paul</h6>
                                                <p className="mb-0">Hawaii, USA</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Slider>
                        </div>
                    </section>

                    <section  className="become pt-md-5 pt-0 "> 
                        <div className="container become-content">
                           <div className="row py-5 mx-0 mb-3 w-100">
                               <div className="col-lg-5 pl-0 col-md-6 left">
                                   <div className="w-100">
                                       {/* <div className="drop col-xl-11 "><img src={require('../../assets/images/drop.svg')} alt="" className=""/></div> */}
                                       <div className="w-100  d-flex align-items-center">    
                                        <div className="user-1" > 
                                            <img src={require('../../assets/images/user-1.png')} alt="" className="d-block mx-auto"/>   
                                        </div>
                                       </div>
                                   </div>
                               </div>
                               <div className="col-lg-7 col-md-6 pl-md-0  pr-md-0 right">
                                   <div>
                                        <div className="text col-xl-10 col-lg-10 pl-0 pr-0 pt-lg-4 pt-0">
                                            <h4 className="mb-4 text-white">Become a <span><img src={require('../../assets/images/logo.svg')} alt="" className=""/> </span> Empath</h4>
                                            <p className="app-desc section-desc mb-4" style={{fontSize:'17px'}}>This is a very important part of our company. As a QE your role is primarily to listen. As a company our job is to bring humanity back to social media while solving the problem of loneliness. Your part is a very essential part of our existence. As a QE you can also earn as you listen. </p>
                                            <div className="get-started">
                                                <Link href=""><p className="mb-0 py-2 px-5 button">Get Started</p></Link>
                                            </div>
                                            
                                       </div>
                                       <div className="user-2 col-lg-6 col-xl-7 px-0 ml-auto d-none d-lg-block" >
                                                <img src={require('../../assets/images/user-2.svg')} alt="" className="ml-auto d-block"/>
                                            </div>
                                   </div>
                               </div>
                           </div>
                           <div className="mt-5 pb-5 mb-md-4 mb-lg-0">
                                <h4 className="mb-4 text-white text-center mb-4 px-2 px-md-0">Take the QE Test to see if you qualify</h4>
                                <div className="get-started d-flex justify-content-center mb-3">
                                    <Link href=""><p className="mb-0 py-2 px-5 button">Get Started</p></Link>
                                </div>
                           </div>
                        </div>
                    </section>

                    <section  className="download-now"> 
                        <div className="container">
                           <div className="row align-items-center">
                               <div className="col-lg-5 col-md-5 h-100 pt-5 pt-md-0">
                                   <div className="w-100 left">
                                       <div className="w-100 h-100 d-flex justify-content-center d-md-block">    
                                            <img src={require('../../assets/images/llustration-2.svg')} alt="" className="col-10 col-md-12 px-0 d-block"/>
                                       </div>
                                   </div>
                               </div>
                               <div className="col-lg-7 col-md-7 pl-xl-5 pl-3">
                                   <div className="right">
                                        <div className="pt-md-5 pb-md-5 pt-0 pb-5 pt-lg-0  pb-lg-0">
                                            <h4 className="mb-4 text-white text-center text-md-right">Download <span><img src={require('../../assets/images/logo.svg')} alt="" className=""/> </span> for free</h4>
                                            <div className=" get-started d-flex flex-wrap mx-auto justify-content-center justify-content-md-end">
                                                <Link to="" className="mr-md-4 mr-3 mb-3 mb-md-0"><img src={require('../../assets/images/playstore.svg')} alt="" /></Link>
                                                <Link to="" className=""><img src={require('../../assets/images/appstore.svg')} alt="" /></Link>
                                            </div>
                                       </div>
                                   </div>
                               </div>
                           </div>
                        </div>
                    </section>
                    <Footer/>
                    
                </UI.Wrapper>
            </div>
        )
    }
}

